

<template>
    <b-overlay :show="isLoading">
    <div >
        <h3>{{ title }}</h3>
        <div class="row">
            <div class="col" v-if="showMonth">
                <div class="form-group">
                    <label>Mois </label>
                    <input v-model="month2" class="form-control" type="month"/>
                </div>
            </div>
            <div class="col"  v-if="showValidation">
                <label>validation </label>
                <v-select v-model="validation" :options="validationsData" label="Selectlabel"/>
            </div>
        </div>
        <div style="height: 500px">
            <ag-charts-vue :key="optionsKey2" :options="options2"></ag-charts-vue>
        </div>
    </div>
    </b-overlay>
</template>

<script>
import CustomFiltre from "@/components/CustomFiltre.vue";
import CustomSelect from "@/components/CustomSelect.vue";
import VueApexCharts from 'vue-apexcharts'
// import {BCol, BRow} from 'bootstrap-vue'
import moment from "moment";

import VSelect from "vue-select";
import Chart from "chart.js";
import {AgChartsVue} from 'ag-charts-vue';

const $earningsStrokeColor2 = 'rgba(2,255,114,0.4)'
const $earningsStrokeColor3 = '#d1124a'
export default {
    name: "HomeView",
    components: {
        VSelect, CustomSelect,
        CustomFiltre,
        'ag-charts-vue': AgChartsVue,
        VueApexCharts,
    },
    data() {
        return {
            isLoading: true,
            validationsData: [],
               month2: "",

            validation: "",
            faction2: "",
            options2: null,
            optionsKey2: 0,
        };
    },
    props:{
      chartTitle:{
          default:'Presences/Abscences collecter au travers des listings'
      },
      title:{
          default:''
      },
      subTitle:{
          default:'Jour du mois'
      },
        modelListing:{
          default:0
      },
        showMonth:{
          default:true
      },
        showValidation:{
          default:true
      },
        week:{
          default:true
      },
        validation:{
          default:0
      }
    },
    computed: {
        extrasData: function () {
            let params = {}
            if (this.month) {
                params.month = this.month
            }
            if (this.pointeuses) {
                params.pointeuses = this.pointeuses
            }
            if (this.postes) {
                params.postes = this.postes
            }
            if (this.clients) {
                params.clients = this.clients
            }
            if (this.zones) {
                params.zones = this.zones
            }
            if (this.faction) {
                params.faction = this.faction
            }
            if (this.faction2) {
                params.faction2 = this.faction2
            }
            if (this.month2) {
                params.month2 = this.month2
            }
            if (this.validation) {
                params.validation = this.validation
            }
            if (this.pointeuses2) {
                params.pointeuses2 = this.pointeuses2
            }
            if (this.directionselectionner) {
                params.directionselectionner = this.directionselectionner
            }

            return params;

        },

    },
    watch: {
        month2: {
            handler: function (after, before) {
                this.getStats2();
            },
            deep: true,
        },
        validation: {
            handler: function (after, before) {
                this.getStats2();
            },
            deep: true,
        },
    },
    created() {
           },

    mounted() {
        this.getStats2();

    },
    methods: {


        getStats2() {
            this.isLoading=true;
            let extra = this.extrasData
            extra.action = 'GetPointagesStats2'
            extra.modelListing=this.modelListing
            extra.week=this.week
            let donnes = Object.keys(extra).map(key => `${key}=${extra[key]}`).join('&')

            let url = "/api/pointages/action?" + donnes;
            this.axios
                .get(url)
                .then((response) => {
                    this.options2 = {
                        autoSize: true,
                        data: response.data,
                        theme: {
                            palette: {
                                fills: ['#43a72c', '#d7114b', '#9BC53D', '#E55934', '#FA7921'],
                                strokes: ['#4086a4', '#b1a235', '#6c8a2b', '#a03e24', '#af5517'],
                            },
                            overrides: {
                                bar: {
                                    series: {
                                        strokeWidth: 0,
                                    },
                                },
                            },
                        },
                        title: {
                            text: this.chartTitle,
                            fontSize: 18,
                            spacing: 25,
                        },
                        footnote: {},
                        series: [
                            {
                                type: 'column',
                                stacked: true,
                                yName: 'Present',
                                xKey: 'jour',
                                yKey: 'pointagesConnu',
                            },
                            {
                                type: 'column',
                                stacked: true,
                                yName: 'Abscent',
                                xKey: 'jour',
                                yKey: 'pointagesInConnu',
                            },
                        ],
                        axes: [
                            {
                                type: 'number',
                                position: 'left',
                            },
                            {
                                type: 'category',
                                position: 'bottom',
                                label: {
                                    rotation: 40,
                                },
                                title: {
                                    enabled: true,
                                    text: this.subTitle,
                                },
                            },
                        ],
                    }
                    this.optionsKey2++;

                    this.isLoading=false;

                })
                .catch((error) => {
                    console.log(error);
                });
        },

    },
};
</script>
